.nav {

  width: calc(100% - 20px);
  max-width: 1140px;
  position: fixed;
  z-index: 10;
  top: 0px;
  transform: translate(-50%, 0);
  left: 50%;
  padding: 15px 0;
  background: #444;


  .container {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    background: #222;
    border: 3px solid chartreuse;
    padding: 20px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 600;
    box-sizing: border-box;
  }
}

.item {
  height: 30px;

}

.item a:hover {
  color: yellow;
  background: #444;
}

.item a {
  text-align: center;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  color: chartreuse;
  text-decoration: none;
  height: 100%;
  width: 100%;
  padding: 8px;
  border-radius: 10px;
}

.item .active {
  color: yellow;
}

.settings {
  margin-top: 20px;
}

@media (max-width: 600px) {
  .nav {
    .item a {
      font-size: 16px;
    }
  }
}

@media (max-width: 425px) {
  .nav {
    padding: 7px;
    .container {
      flex-wrap: wrap;
      padding: 7px;

      .item {
        width: 50%;
      }
    }
  }
}