.root {
  position: relative;
  background: #222;
  width: 19%;
  margin-bottom: 30px;
  margin-top: 10px;
  border: 2px solid chartreuse;
  box-sizing: border-box;
  color: yellow;

  .root_block {
    position: relative;
    height: 100%;

    .title_block {
      height: 100%;
      width: 100%;
      background: #222;
      color: yellow;
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      padding: 0;

      .content {
        display: flex;
        flex-direction: column;
      }

      .text {
        font-weight: 400;

        &:nth-child(even) {
          font-weight: 700;
          color: chartreuse;
        }
      }
    }
  }
}

@media (min-width: 769px) {
  .root {
    margin-right: 1.25%;
    
    &:nth-child(5n) {
      margin-right: 0;
    }  
  }
}

@media (max-width: 768px) {
  .root {
    width: 24%;
    margin-right: 1.3333%;
    margin-bottom: 10px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}

@media (max-width: 500px) {
  .root {
    width: 49%;
    margin-right: 2%;

    &:nth-child(even) {
      margin-right: 0;
    }
  }
}
